@import url("https://fonts.googleapis.com/css2?family=Public+Sans:wght@100;200;300;400;500;600;700&family=Source+Sans+3:wght@400;600;700&display=swap");

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-family: "Public Sans", sans-serif;
}

a {
  text-decoration: none;
  color: inherit;
}

.app {
  width: 100vw;
  height: 100vh;
}

body {
  overflow-x: hidden;
}
